/**
 * Typography
 */
/**
 * Fonts
 */
h1, h2, h3, h4, h5, h6 {
  margin-bottom: .5rem;
  margin-top: .3em;
  font-family: "Fira Sans", "Lato", "Open Sans", "Gill Sans MT", "Gill Sans", Corbel, Arial, sans-serif;
  font-weight: normal; }
  h1 small, h2 small, h3 small, h4 small, h5 small, h6 small {
    color: #a8b2c7;
    font-size: .75em; }

p {
  margin-bottom: 1rem;
  line-height: 1.75;
  font-weight: 400; }

span.hint, p.hint, a.hint {
  color: #a8b2c7;
  font-style: italic;
  font-size: .85em; }

span.thin, p.thin, a.thin {
  font-weight: 200; }

span.ultra-thin, p.ultra-thin, a.ultra-thin {
  font-weight: 100; }

a {
  color: #1483ff;
  text-decoration: none; }

/**
 * Code
 */
pre, code {
  display: block; }

pre {
  padding: 1rem;
  background: #282a36;
  color: #f8f8f2;
  margin: .5rem 0;
  font-family: "Inconsolata", "Monaco", "Consolas", "Andale Mono", "Bitstream Vera Sans Mono", "Courier New", Courier, monospace;
  overflow-x: auto;
  line-height: 1.45;
  tab-size: 2;
  -webkit-font-smoothing: auto;
  -webkit-text-size-adjust: none;
  position: relative;
  border-radius: 2px;
  font-size: 0.8rem; }

code {
  margin: 0;
  padding: 0;
  overflow-wrap: break-word;
  white-space: pre-wrap; }

/**
 * Lists
 */
ol, ul {
  margin-top: 1em;
  display: block;
  padding-left: 1rem;
  margin-bottom: 1em; }

ol {
  font-variant-numeric: tabular-nums;
  font-feature-settings: 'tnum' 1;
  list-style-type: decimal; }

ul {
  list-style-type: square; }

.list-reset,
.list-reset > li {
  padding: 0;
  margin: 0;
  list-style: none; }

/**
 * Forms
 */
/**
 * Form Element Inputs
 */
input[type=number],
input[type=tel],
input[type=text],
input[type=password],
textarea {
  display: inline-block;
  box-sizing: border-box;
  outline: none; }

.form-input {
  background: #333b4c;
  border: solid 1px #455066;
  color: #d9dce1;
  transition: box-shadow 200ms;
  border-radius: 0;
  font-size: 13px;
  height: 32px;
  line-height: 32px;
  width: 100%;
  padding: 6px;
  margin-bottom: 1em; }
  .form-input:focus {
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12); }
  .form-input[disabled] {
    cursor: not-allowed;
    color: #72809b; }

textarea.form-input {
  min-height: 120px;
  line-height: 1.3em; }

select {
  background: #333b4c;
  border: solid 1px #455066;
  color: #d9dce1;
  border-radius: 2px;
  height: 32px;
  line-height: 32px;
  font-size: 13px;
  width: 100%; }
  select:focus {
    outline: none; }
  select[disabled] {
    cursor: not-allowed;
    color: #72809b; }

/**
 * Components
 */
.branding {
  text-transform: lowercase;
  font-weight: 100;
  color: #c0ddff; }
  .branding .branding-name {
    font-size: 1.8rem;
    display: inline-block;
    vertical-align: top; }
  .branding .branding-logo {
    font-size: 1.2rem; }

.section {
  padding: 1.8em;
  margin-bottom: 2em; }

.tag {
  cursor: default;
  border-radius: 3px;
  display: inline-block;
  margin: 0 8px 0 0;
  box-sizing: border-box;
  position: relative;
  background: #fff;
  color: #13141b;
  height: 1rem;
  line-height: 1rem;
  font-size: 1rem;
  padding: 0 .2rem; }
  .tag.tag-small {
    height: .9rem;
    line-height: .9rem;
    font-size: .75rem;
    padding: 0 .1rem; }
  .tag.tag-large {
    height: 1.2rem;
